.grid {
    display: inline-grid;
    grid-template-columns: 50px 50px 50px 50px;
    grid-template-rows: 50px 50px 50px 50px;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    -ms-flex-pack: center;
        justify-content: center;
    background-color: white;
    padding: 5px;
    cursor: default;
}

.grid__cell {
    background-color: blue;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.grid_disabled {
    opacity: 0.8;
}



